import { motion } from 'framer-motion';
import Link from 'next/link';
import Image from 'next/image';
import { useInView } from 'react-intersection-observer';
import { useTranslation } from 'next-i18next';
import { imageData } from '@/data/images';

const featuredWorks = [
  {
    id: 1,
    title: "타로카드 읽는 카페",
    description: "브런치북 12회 대상작. 타로를 통해 마음을 읽는 이야기.",
    image: imageData.books[0].cover,
    link: "/books",
    type: "book"
  },
  {
    id: 2,
    title: "2026년 1월 월간플로라",
    description: "새해를 맞이하는 에코 플라워 레시피",
    image: imageData.flowers.monthly[0].src,
    link: "/flowers/monthly-flora",
    type: "flora"
  },
  {
    id: 3,
    title: "에코 플라워 스타일링 북",
    description: "자연에 해가 되지 않는 플라워 디자인",
    image: imageData.books[1].cover,
    link: "/books",
    type: "book"
  }
];

export default function FeaturedWork() {
  const { t } = useTranslation('common');
  const [ref, inView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });

  return (
    <section ref={ref} className="section-container bg-white">
      <motion.div
        initial={{ opacity: 0, y: 30 }}
        animate={inView ? { opacity: 1, y: 0 } : {}}
        transition={{ duration: 0.8 }}
        className="text-center mb-16"
      >
        <h2 className="text-accent-terracotta mb-4">{t('featured_title')}</h2>
        <div className="w-16 h-1 bg-background-beige mx-auto" />
      </motion.div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 md:gap-12">
        {featuredWorks.map((work, index) => (
          <WorkCard key={work.id} work={work} index={index} inView={inView} />
        ))}
      </div>
    </section>
  );
}

function WorkCard({ work, index, inView }) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 40 }}
      animate={inView ? { opacity: 1, y: 0 } : {}}
      transition={{ duration: 0.8, delay: index * 0.2 }}
      className="group"
    >
      <Link href={work.link} data-cursor="pointer">
        <div className="bg-background-linen rounded-organic overflow-hidden shadow-soft hover-organic p-1 h-full flex flex-col">
          {/* Image Container */}
          <div className="relative aspect-[16/10] rounded-[12px] overflow-hidden mb-6">
            <Image
              src={work.image}
              alt={work.title}
              fill
              className="object-cover transition-transform duration-700 group-hover:scale-105"
            />
            {/* Overlay Tag */}
            <div className="absolute top-4 left-4">
              <span className="px-3 py-1 bg-white/80 backdrop-blur-sm rounded-full text-[10px] uppercase tracking-widest font-bold text-accent-sage">
                {work.type}
              </span>
            </div>
          </div>

          {/* Content */}
          <div className="px-4 pb-6 flex-grow">
            <h3 className="text-xl mb-3 text-text-warm group-hover:text-accent-rose transition-colors">
              {work.title}
            </h3>
            <p className="text-sm text-text-warm/70 line-clamp-2 leading-relaxed mb-6">
              {work.description}
            </p>
            <span className="text-xs uppercase tracking-widest font-bold text-accent-rose flex items-center gap-2">
              View More 
              <motion.span
                animate={{ x: [0, 5, 0] }}
                transition={{ duration: 1.5, repeat: Infinity }}
              >
                →
              </motion.span>
            </span>
          </div>
        </div>
      </Link>
    </motion.div>
  );
}
